import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import PickupBarOptions from './PickupBarOptions';

export default class PickupBarOptionsFrame extends Component {

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.iframe.addEventListener('load', () => {
            const frameDocument = this.iframe.contentDocument;
            const frameBody = frameDocument.body;
            frameBody.style.margin ="0px";
            frameBody.style.backgroundColor = "#FFF";
            frameBody.style.height = "135px";
            frameBody.style.boxSizing = "border-box";
            const hostDiv = frameDocument.createElement('div');
            frameBody.appendChild(hostDiv);
            ReactDOM.render(<PickupBarOptions
                                 onOkMsg={this.props.onOkMsg}
                                 onCancelMsg={this.props.onCancelMsg}/>,
                            hostDiv);
        });
        this.iframe.src = "about:blank"; // FF doesn't work without this line.
    }

    render(){
        const frameStyle = {
            "width": "440px",
            "height":"135px",
            "border": "solid 1px rgb(191, 0, 0)"
        }
        return(
            <iframe style={frameStyle} scrolling='no' id='rb_iframe' ref={c => this.iframe = c}
                    src="about:blank">  {/* FF doesn't work without this `src` attribute. */}
            </iframe>
        );
    }
}

PickupBarOptionsFrame.propTypes = {
    onOkMsg: PropTypes.string,
    onCancelMsg: PropTypes.string,
};
