const AMAZON_ASSISTANT_BAR_HEIGHT = 80;
const AMAZON_ASSISTANT_BAR_MAXHEIGHT = 90;
const AMAZON_ASSISTANT_IFRAME_FILTER = 'iframe[id^="PComp-"]';

export default class AmazonAssistantWatcher {
  constructor() {
    this.visible = false;
  }

  set on_visibility_changed(handler) {
    this.on_visibility_changed_handler = handler;
  }

  get visibleHeight() {
    return (this.visible) ? AMAZON_ASSISTANT_BAR_HEIGHT : 0;
  }

  attach() {
    // Check if amazon assistant iframe is injected already.
    this.checkVisible_();

    // Watch for document.body children: amazon assistant iframe can appear/disappear.
    const target_node = document.body;
    const config = {
      attributes: false,
      childList: true,
      subtree: false,
    };
    this.childlist_observer = new MutationObserver(() => {
      this.checkVisible_();
    });
    this.childlist_observer.observe(target_node, config);
  }

  detach() {
    this.childlist_observer.disconnect();
    delete this.childlist_observer;
  }

  //////////////////////////////////////////////////////////////////////////////
  // Private implementation:
  //
  checkVisible_() {
    // Check the bar is visible. Notify if visibility has changed.
    const amazon_assistant_iframes =
        document.body.querySelectorAll(AMAZON_ASSISTANT_IFRAME_FILTER);
    let amazon_assistant_bar_found = false;
    for (let i = 0; i < amazon_assistant_iframes.length; ++i) {
      const node = amazon_assistant_iframes.item(i);
      const node_height =
          (node.offsetHeight > 0) ? node.offsetHeight
                                  : parseInt(node.style.height, 10);
      if (node_height < AMAZON_ASSISTANT_BAR_MAXHEIGHT) {
        amazon_assistant_bar_found = true;
        break;
      }
    }
    if (this.visible != amazon_assistant_bar_found) {
      this.visible = amazon_assistant_bar_found;
      if (this.on_visibility_changed_handler) {
        this.on_visibility_changed_handler();
      }
    }
  }
}
