import { PICKUPBAR_IFRAME_ID, PICKUPBAR_ZINDEX,
         addBarNodeIntoDocument }  from './BarIds';
import { BANNER_DISABLE_OPTION, BANNER_DISABLED_TIMER } from '../../../../utilities/src/setting_config'
import { PICKUP_COUPON_PAGE_TYPE, PICKUP_CLASS_NAME} from '../../../../utilities/src/pickup_const'

import React from 'react';
import {render} from 'react-dom';
import PickupBarOptionsFrame  from '../components/pickupbar/PickupBarOptionsFrame'
import DocumentMarginTopController from './pickup/DocumentMarginTopController';
import AmazonAssistantWatcher from './pickup/AmazonAssistantWatcher';

const PICKUPBAR_VISIBILITY_ANIMATION = "0.8s ease-in-out";
const PICKUPBAR_BOTTOM_BORDER_HEIGHT = 3;

class PickupManagerClass {
    constructor() {
        this.frame = null;
        this.bar_height = 0;
        this.pickupData = null;
    }

    init(proxyStore, _pickup_data) {
        if (proxyStore.state.data.login == "0") {
            return;
        }

        this.pickupData = _pickup_data;
        platform_api_root.runtime.onMessage.addListener((request, sender, sendResponse) => {
            if (request.pickup_type == PICKUP_CLASS_NAME.COUPON_YAHOO_AJAX_PICKUP) {
                if (request.pickup_data) {
                    this.reload(request.pickup_data);
                } else {
                    this.remove()
                }
            }
        });

        this.inject();

        const _page_type = _pickup_data.matched_domain_details.page_type;

        window.addEventListener('message', ((e) => {
            let msg = e.data;
            if (!(msg && msg.method)) {
                return;
            }

            if (msg.method == 'RTBcs.PickupManager.getData') {
                this.frame.contentWindow.postMessage({
                    method: "setData",
                    value: this.pickupData
                }, '*');
            } else if (msg.method == 'RTBcs.PickupManager.close') {
                let extensionSettings = proxyStore.getState().data.ExtensionSetting;
                switch (msg.index) {
                    case 1:
                        // 24 hours
                        extensionSettings.PickupDisabledTimerSelectedOption = BANNER_DISABLED_TIMER.TIMER_24HOURS;
                        extensionSettings.BannerDisableOption = BANNER_DISABLE_OPTION.BANNER_DISABLE_SEARCH;
                        break;
                    case 2:
                        // 30 days
                        extensionSettings.PickupDisabledTimerSelectedOption = BANNER_DISABLED_TIMER.TIMER_30DAYS;
                        extensionSettings.BannerDisableOption = BANNER_DISABLE_OPTION.BANNER_DISABLE_SEARCH;
                        break;
                    case 0:
                        // only this time
                        break;
                }
                proxyStore.dispatch({ type: 'SET_EXTENSION_SETTING', data: extensionSettings });

                this.remove();
            } else if (msg.method == 'RTBcs.PickupManager.closeHover') {
                this._removeHover();
            } else if (msg.method == "RTBcs.PickupManager.closeBar") {
                if (_page_type === PICKUP_COUPON_PAGE_TYPE.SEARCH) {
                    this._injectHover();
                } else {
                    this.remove();
                }
            }
        }));
    }

    setupPopupStyle() {
        this.frame.setAttribute('id', PICKUPBAR_IFRAME_ID);
        this.frame.setAttribute('scrolling', "no");
        this.frame.style.border = "none";
        this.frame.style.position = "fixed";
        this.frame.style.top = this.ideal_top_position + "px";
        this.frame.style.left = "0px";
        this.frame.style.right = "auto";
        this.frame.style.bottom = "auto";
        this.frame.style.verticalAlign = "baseline";
        this.frame.style.margin = "0";
        this.frame.style.padding = "0";
        this.frame.style.zIndex = PICKUPBAR_ZINDEX;
        this.frame.style.width = "100%";
        this.frame.style.height = this.bar_height + "px";
        this.frame.style.display = "block";
        this.frame.style.transition = "top " + PICKUPBAR_VISIBILITY_ANIMATION;
        this.frame.style.boxSizing = "content-box";
        this.frame.style.borderBottom = PICKUPBAR_BOTTOM_BORDER_HEIGHT + "px solid rgb(191, 0, 0)";
        this.frame.style.minWidth = "840px"
    }

    inject() {
        this.frame = document.createElement("iframe");
        this.bar_height = this.pickupData.matched_domain_details.height;
        this.ideal_top_position = 0 - this.bar_height;
        this.setupPopupStyle();

        this.frame.addEventListener('load', () => {
            // Start frame showing animation.
            this.ideal_top_position = 0;
            this.frame.style.top = this.ideal_top_position + "px";

            if (!this.document_margin_top_controller) {
                this.document_margin_top_controller =
                    new DocumentMarginTopController(
                        this.pickupData.matched_domain_details.scid,
                        PICKUPBAR_VISIBILITY_ANIMATION);
            }
            this.document_margin_top_controller.setMarginTop(this.bar_height);

            if (!this.amazon_assistant_watcher) {
                this.amazon_assistant_watcher = new AmazonAssistantWatcher();
                this.amazon_assistant_watcher.on_visibility_changed =
                    this.onAmazonAssistantVisibilityChanged.bind(this);
                this.amazon_assistant_watcher.attach();
            }
        });

        this.frame.src = platform_api_root.runtime.getURL('content_pickupbar.html');
        addBarNodeIntoDocument(this.frame);
    }

    //Handle Yahoo Shopping Ajax Queries
    reload(_new_pickup_data) {
        this.remove(/*due_reload_with_pickup_data=*/_new_pickup_data);
    }

    onAmazonAssistantVisibilityChanged() {
        // If our bar is visible, then maybe adjust the top to account for
        // Amazon Assistant.
        if (this.amazon_assistant_watcher && this.ideal_top_position >= 0) {
            this.frame.style.top = this.amazon_assistant_watcher.visibleHeight +
                                   this.ideal_top_position + "px";

            if (this.document_margin_top_controller) {
                this.document_margin_top_controller.setMarginTop(
                    this.amazon_assistant_watcher.visibleHeight + this.bar_height);
            }

            this._updateHoverTop(document.getElementById("coupon_hover_div"));
        }
    }

    cleanup() {
        this.frame = document.getElementById(PICKUPBAR_IFRAME_ID);
        if (this.frame) {
            this.bar_height = parseInt(this.frame.style.height, 10);
            this.remove();
            this.frame = null;
            this.pickupData = null;
            DocumentMarginTopController.cleanup();
        }
    }

    remove(due_reload_with_pickup_data = null) {
        this._removeHover();

        if (this.frame && this.frame.parentElement) {
            // Remove the frame after the closing animation is done.
            const frame_being_removed = this.frame;
            this.frame.addEventListener('transitionend', () => {
                document.body.removeChild(frame_being_removed);
                if (this.frame && this.frame.parentElement) {
                    // If a new injection is already started, don't restore the
                    // document margins - they are increasing back now.
                    return;
                }

                if (this.document_margin_top_controller) {
                    this.document_margin_top_controller.detach();
                    delete this.document_margin_top_controller;
                }

                if (due_reload_with_pickup_data) {
                    this.pickupData = due_reload_with_pickup_data;
                    this.inject();
                }
            });

            // Start frame closing animation.
            this.ideal_top_position = 0 - this.bar_height;
            this.frame.style.top = this.ideal_top_position + "px";
            this.frame = null;

            if (this.document_margin_top_controller) {
                const margin_top_reset =
                    (this.amazon_assistant_watcher)
                        ? this.amazon_assistant_watcher.visibleHeight
                        : 0;
                this.document_margin_top_controller.setMarginTop(margin_top_reset);
            }

            if (this.amazon_assistant_watcher) {
                this.amazon_assistant_watcher.on_visibility_changed = null;
                this.amazon_assistant_watcher.detach();
                delete this.amazon_assistant_watcher;
            }
        }
        else if (due_reload_with_pickup_data) {
            this.pickupData = due_reload_with_pickup_data;
            this.inject();
        }
    }

    _injectHover() {
        let hoverDiv = document.getElementById("coupon_hover_div");
        if (!hoverDiv) {
            hoverDiv = document.createElement('div');
            hoverDiv.setAttribute("id", "coupon_hover_div");

            hoverDiv.style.position = "fixed";
            this._updateHoverTop(hoverDiv);
            hoverDiv.style.right = "0px";
            hoverDiv.style.zIndex = PICKUPBAR_ZINDEX;
            hoverDiv.style.backgroundColor = "#fff";

            addBarNodeIntoDocument(hoverDiv);
            render(<PickupBarOptionsFrame
                        onOkMsg="RTBcs.PickupManager.close"
                        onCancelMsg="RTBcs.PickupManager.closeHover"/>,
                   hoverDiv);
        }
    }

    _updateHoverTop(hoverDiv) {
        if (hoverDiv) {
            const top_shift =
                (this.amazon_assistant_watcher)
                    ? this.amazon_assistant_watcher.visibleHeight
                    : 0;
            hoverDiv.style.top = (top_shift + this.bar_height +
                                  PICKUPBAR_BOTTOM_BORDER_HEIGHT) + "px";
        }
    }

    _removeHover() {
        const hDiv = document.getElementById("coupon_hover_div");
        if (hDiv) {
            document.body.removeChild(hDiv);
        }
    }
}

const PickupManager = new PickupManagerClass();
export default PickupManager;