import {Store} from 'react-chrome-redux';

import PickupManager from './controllers/PickupManager';
import AffiliateManager from './controllers/AffiliateManager';
import ToolbarManager from './controllers/ToolbarManager';
import RebatesManager from './controllers/RebatesManager';

import {parseURL, getURLParam, sendMessageByPort} from '../../../utilities/src/common_helper';
import {REBATES_MATCH_PATTERN} from '../../../utilities/src/rebates_const'
import {PICKUP_CLASS_NAME} from '../../../utilities/src/pickup_const'
import {checkRuntimeValid} from '../../../utilities/src/runtime_helper';
import {CONST_RAKUTEN_WEBSEARCH} from  '../../../utilities/src/setting_config'

import PlatformSpecific from 'PlatformSpecific';

let pageState = {
    windowIsReady : false,
    proxyStoreIsReady : false
};
pageState.waitForWindowIsSafeForInjections = new Promise(function(resolve, reject) {
    pageState.resolveWindowSafeForInjectionsPromise_ = resolve;
});
pageState.setWindowIsSafeForInjections = function() {
    pageState.resolveWindowSafeForInjectionsPromise_();
};

let proxyStore = null, port = null;

const initPorts = () => {
  pageState.proxyStoreIsReady = false;
  proxyStore = new Store({portName: 'background_data', extensionId: null});

  port = platform_api_root.runtime.connect({name: "callFromContentScript"});

  port.onMessage.addListener(function(msg) {
    // background returns callback
    if (msg.method == "tbuid") {
        RTBcs._runTBI(msg.tbuid);
    }
    if (msg.rebates_popup) {
        if (msg.rebates_info != undefined) {
            pageState.waitForWindowIsSafeForInjections.then(() => {
                RTBcs.injectRebatesPopup(msg);
            });
        }
    }
    if (msg.show_rebates_campaign_hover) {
        pageState.waitForWindowIsSafeForInjections.then(() => {
            RTBcs.injectRebatesCampaign(msg.toolbar_campaign);
        });
    }
    if (msg.pickup_type === PICKUP_CLASS_NAME.COUPON_PICKUP) {
        pageState.waitForWindowIsSafeForInjections.then(() => {
            RTBcs.injectPickupbar(msg.pickup_data);
        });
    }
    if (msg.pickup_type === PICKUP_CLASS_NAME.ICHIBA_PICKUP) {
        pageState.waitForWindowIsSafeForInjections.then(() => {
            RTBcs.injectPickupbar(msg.pickup_data);
        });
    }
    if (msg.pickup_type === PICKUP_CLASS_NAME.AFFILIATE_PICKUP) {
        pageState.waitForWindowIsSafeForInjections.then(() => {
            RTBcs.injectAffiliatebar(msg.pickup_data);
        });
    }
  });

  // Keep an eye on the extension enabled state: if it becomes disabled, we have
  // to remove any html injections we made.
  // `port.onDisconnect` actually works only in Chrome currently but it will
  // probably work in Firefox too soon.
  port.onDisconnect.addListener(() => {
    const cleanupIfExtensionIsOff = () => {
      checkRuntimeValid((alive) => {
        if (!alive) {
          RTBcs.cleanupAllHtmlInjections();
        }
      });
    };
    // Need to delay the check a bit to ensure the background script is shutdown
    // completely before the check.
    setTimeout(cleanupIfExtensionIsOff, 0);
  });
};

var RTBcs =
    {
        startPages: [
            /^(http|https):\/\/websearch.rakuten.co.jp\/static\/platform_identifier\/install.html$/i,
            /^(http|https):\/\/websearch.rakuten.co.jp\/static\/platform_identifier\/welcome.html/i,
            /^(http|https):\/\/websearch.rakuten.co.jp\/static\/platform_identifier\/install.html\?scid=platform_installparam$/i
        ],

        //デフォルトエンジンの判定に使用
        searchProviders: [
            {
                host: "www.google.co.jp",
                queryKey: "q"
            },
            {
                host: "www.google.com",
                queryKey: "q"
            },
            {
                host: "search.yahoo.co.jp",
                queryKey: "p"
            },
            {
                host: "www.bing.com",
                queryKey: "q"
            },
            {
                host: "search.goo.ne.jp",
                queryKey: "MT"
            }
        ],

        skipGetRtbSessForUrls: [
            /^http(s)?:\/\/websearch.rakuten.co.jp\/logout/i,
        ],

        //ログインチェックをするためのフック（アイコン変更＆xml/user投げる）
        loginCheckers: [
            /^(http|https):\/\/websearch.rakuten.co.jp\/pointMailControl/i,
            /^(http|https):\/\/grp03.id.rakuten.co.jp\/rms\/nid\/logout/i,
            /^(http|https):\/\/member.id.rakuten.co.jp\/rms\/nid/i,
            /^(http|https):\/\/stg.login.account.rakuten.com\/sso\/authorize/i,
            /^(http|https):\/\/login.account.rakuten.com\/sso\/authorize/i,
            new RegExp(REBATES_MATCH_PATTERN, 'i')
        ],

        //シンプルトップ（ログイン完了後に飛んでくる。アイコンは変えるが、xml/userは投げない）
        loginOnlyCheckers: [
            /^(http|https):\/\/websearch.rakuten.co.jp\/SimpleTop/i
        ],

        // タゲメの一覧更新ボタン押下のチェックの為のフック
        messageListUpdateCheckers: [
            /^(http|https):\/\/msg.websearch.rakuten.co.jp\/view\/AllMsg/i
        ],

        // List of host patterns where we have to delay html injections.
        hostPatternsToDelayHtmlInjections: [
            /^.*\.omni7\.jp$/i
        ],

        matchLocationWithPatterns: function(patterns) {
            return patterns.some((item) => item.test(window.location.href));
        },

        //ページ読み込みごとに呼ばれる
        init: function() {
            // Firefox & Old Edge don't call `port.onDisconnect` (see above), so at least
            // remove any broken injections when/if the extension is enabled back (or e.g.
            // a reload or update happened).
            this.cleanupAllHtmlInjections();

            initPorts();
            const unsubscribe = proxyStore.subscribe(() => {
                unsubscribe(); // make sure to only fire once
                pageState.proxyStoreIsReady = true;

                this.checkStartPage();
                this.checkSearchProvider();
                this.checkLogin();
                this.openSetting();
                this.checkAndUpdateRebatesBridge();
                platform_api_root.runtime.sendMessage({ method: "loaded" });
                this.checkListUpdate();

                pageState.waitForWindowIsSafeForInjections.then(() => {
                    this.sendURLLocation();
                    ToolbarManager.init(proxyStore);
                });
            });
        },

        cleanupAllHtmlInjections: function() {
            ToolbarManager.cleanup();
            RebatesManager.cleanup();
            PickupManager.cleanup();
            AffiliateManager.cleanup();
        },

        checkAndUpdateRebatesBridge: function() {
            let url = window.location.href;
            let regExp = new RegExp(REBATES_MATCH_PATTERN);
            if (regExp.test(url)) {
                let hidden_div = document.getElementById('r_ext_bridge');
                if(hidden_div){
                    hidden_div.innerText = "true";
                }
            }
        },

        checkUrlNeedsDelayedHtmlInjections: function(_url) {
            let current_url = parseURL(_url);
            for (const host_pattern of RTBcs.hostPatternsToDelayHtmlInjections) {
                if (host_pattern.test(current_url.host)) {
                    return true;
                }
            }
            return false;
        },

        //デフォルトエンジン変更ページでのみ呼ばれる
        checkStartPage: function() {
            if (this.matchLocationWithPatterns(this.startPages)) {
                platform_api_root.runtime.sendMessage({ method: "deleteSpFlag" });
                this._getTbuidAndRunTBI();
            }
        },

        synchronizeHistory: function(obj){
            if(obj && obj.detail && obj.detail.type === "update"){
                let dataArr = obj.detail.data;
                if(dataArr && dataArr.length >0){
                    proxyStore.dispatch({ type: 'CLEAR_HISTORY' });
                    for (let i in dataArr){
                        proxyStore.dispatch({ type: 'ADD_HISTORY', data: {local:false, input:dataArr[i].toString() }});
                    }
                }
            }
            else if(obj && obj.detail && obj.detail.type === "delete"){
                proxyStore.dispatch({ type: 'CLEAR_HISTORY' });
            }
        },

        synchronizeHistoryPreference: function(obj) {
            if (obj && obj.detail){
                let data = obj.detail.data;
                let extensionSetting = proxyStore.getState().data.ExtensionSetting;
                if (extensionSetting) {
                    extensionSetting.IsSearchHistoryStored = (data != "1");
                    proxyStore.dispatch({
                        type: 'SET_EXTENSION_SETTING',
                        data: extensionSetting
                    });
                }
            }
        },
        clearServerHistory: function(){
            //TODO need to fix
            //localStorage.web_search_history_ = "";
        },
        injectCrossCommunicationDiv: function(){
            let croosCommDiv = document.createElement("div");
            croosCommDiv.setAttribute("id", "chr_histdata_div");
            croosCommDiv.style.display = "none";
            croosCommDiv.addEventListener("historyEvent", RTBcs.synchronizeHistory, false);
            croosCommDiv.addEventListener("onOffEvent", RTBcs.synchronizeHistoryPreference, false);

            let crossCommDivText = "0";
            const extensionSetting = proxyStore.getState().data.ExtensionSetting;
            if (extensionSetting && extensionSetting.IsSearchHistoryStored) {
                crossCommDivText = "1";
            }

            croosCommDiv.innerText = crossCommDivText;
            document.body.appendChild(croosCommDiv);
        },

        injectRebatesPopup: function(data) {
            const { hover_images, rebates_info, is_activated } = data;
            RebatesManager.injectPopup(proxyStore, hover_images, rebates_info, is_activated);
        },
        injectRebatesCampaign(toolbar_campaign) {
            RebatesManager.injectCampaign(proxyStore, toolbar_campaign);
        },
        injectPickupbar: function(_pickup_data) {
            PickupManager.init(proxyStore, _pickup_data);
        },
        injectAffiliatebar: function(_affiliate_data) {
            AffiliateManager.init(_affiliate_data);
        },

        //ツールバーユーザIDを作成
        _getTbuidAndRunTBI: function() {
            sendMessageByPort(port, "tbuid");// port.onMessage handler calls runTBI() then.
        },

        //デフォルトエンジンをチェック(楽天 or Other)
        checkSearchProvider: function() {
            let current_url = parseURL(window.location.href);
            const url_host_is_rakuten_websearch =
                (current_url.host == CONST_RAKUTEN_WEBSEARCH.host);

            if (url_host_is_rakuten_websearch) {
                platform_api_root.runtime.sendMessage({ method: "clearServerHistory" });
                this.injectCrossCommunicationDiv();

                if (document.referrer == "" && getURLParam(window.location.href, CONST_RAKUTEN_WEBSEARCH.queryKey)) {
                    platform_api_root.runtime.sendMessage({ method: "default", value: true });
                }
                this._getTbuidAndRunTBI();
            }
            if (document.referrer == "") {
                this.searchProviders.find((item) => {
                    if (current_url.host != item.host) {
                        return false;
                    }

                    // Check also that "search terms" parameter is in the URL.
                    let url_to_parse = window.location.href;
                    if (item.host == "www.google.co.jp") {
                        const googleUrl = window.location.href.replace("#", "");
                        url_to_parse = googleUrl;
                    }
                    if (!getURLParam(url_to_parse, item.queryKey)) {
                        return false;
                    }

                    platform_api_root.runtime.sendMessage({ method: "default", value: false });
                    return true;
                });
            }
        },

        //ToolBarInit実行
        _runTBI: function(tbuid) {
            let tbiObject = {
                UserID: tbuid,
                toolid: 1
            };

            let bridgeEvent = document.createEvent('Event');
            bridgeEvent.initEvent('bridgeEvent', true, true);

            function fireBridgeEvent(data) {
                let hiddenDiv = document.getElementById('bridge');
                if(hiddenDiv){
                    hiddenDiv.innerText = data;
                    hiddenDiv.dispatchEvent(bridgeEvent);
                }
            }
            fireBridgeEvent(JSON.stringify(tbiObject));

            if (!this.matchLocationWithPatterns(this.skipGetRtbSessForUrls)) {
                platform_api_root.runtime.sendMessage({ method: "getRtbSess" });
            }
        },

        sendURLLocation: function() {
            sendMessageByPort(port, {
                method: "urlLocation",
                referer: document.referrer,
                title: document.title
            });
        },

        //ログインチェック
        checkLogin: function() {
            if (this.matchLocationWithPatterns(this.loginCheckers)) {
                platform_api_root.runtime.sendMessage({ method: "checkLogin" });
                return;
            }

            if (this.matchLocationWithPatterns(this.loginOnlyCheckers)) {
                platform_api_root.runtime.sendMessage({ method: "checkLoginOnly" });
                return;
            }

            platform_api_root.runtime.sendMessage({ method: "checkLoginTimer" });
        },

        // メッセージ一覧更新ボタンを押した場合
        checkListUpdate: function() {
            if (this.matchLocationWithPatterns(this.messageListUpdateCheckers)) {
                platform_api_root.runtime.sendMessage({ method: "messageListUpdate" });
                return;
            }
        },

        //chrome://settings/searchEnginesを開く
        openSetting: function() {
            if (this.matchLocationWithPatterns(this.startPages)) {
                const sebtn = document.querySelector(".sebtn");
                if (sebtn) {
                    sebtn.addEventListener("click", function() {
                        platform_api_root.runtime.sendMessage({ method: "opensetting" });
                    });
                }
            }
        }
    };

platform_api_root.runtime.onMessage.addListener(function(request, sender, sendResponse) {
    if (request.method == "clearServerHistory") {
        RTBcs.clearServerHistory();
    }
    else if (request.method == "reinit") {
        RTBcs.init();
    }
});

// Decide if need to delay our html injections on this website (e.g. rebates
// domain *.omni7.jp has weird js inside which causes the document object be
// like "rewritten" several times during loading and even after
// `window.onload` event with loosing of already attached event listeners).
const need_delay_html_injections =
        RTBcs.checkUrlNeedsDelayedHtmlInjections(window.location.href);
if (!need_delay_html_injections) {
    pageState.setWindowIsSafeForInjections();
}

window.addEventListener("load", () => {
    pageState.windowIsReady = true;
    PlatformSpecific.ensureProxyStoreReady(pageState, proxyStore);

    if (need_delay_html_injections) {
        window.setTimeout(() => {
            pageState.setWindowIsSafeForInjections()
        }, 1000);
    }
});

RTBcs.init();
