import { REBATES_CAMPAIGN_IFRAME_ID, REBATES_CAMPAIGN_ZINDEX }  from '../InjectionFrameIds';

import RebatesBaseController from './RebatesBaseController';

import { RebatesCampaignHoverState } from '../../../../../utilities/src/rebates_const'

export default class RebatesCampaignController extends RebatesBaseController {
    constructor() {
        super(REBATES_CAMPAIGN_IFRAME_ID);
    }

    inject(proxy_store, campaign) {
        const height = 120;

        if (!super.inject(height, REBATES_CAMPAIGN_ZINDEX)) {
            return;
        }

        this.proxy_store = proxy_store;
        this.campaign = campaign;
    }

    static cleanup() {
        RebatesBaseController.cleanup(REBATES_CAMPAIGN_IFRAME_ID);
    }

    remove() {
        super.remove();

        this.proxy_store = null;
        this.campaign = null;
    }

    onFrameLoadedInitially() {
        this.frame.contentWindow.postMessage({
            method: "renderCampaign",
            campaign: this.campaign,
        }, '*');
    }

    onWindowMessage(e) {
        const msg = e.data;
        if (!(msg && msg.method)) {
            return;
        }

        if (msg.method == 'RTBcs.RebatesManager.forwardCampaign') {
            window.location = this.campaign.forwardUrl;
        }
        else if (msg.method == 'RTBcs.RebatesManager.closeCampaign') {
            const campaign_hover_states =
                this.proxy_store.getState().data.rebates_campaign_hover_states;
            let action_type = 'ADD_REBATES_CAMPAIGN_HOVER_STATE';

            const page_host = window.location.host;
            let campaign_hover_state =
                campaign_hover_states.find((item) => {
                    return item._domainName == page_host;
                });

            if (campaign_hover_state !== undefined) {
                campaign_hover_state._counter++;
                action_type = 'UPDATE_REBATES_CAMPAIGN_HOVER_STATE';
            }
            else {
                campaign_hover_state = new RebatesCampaignHoverState();
                campaign_hover_state._domainName = page_host;
                campaign_hover_state._counter = 1;
            }

            this.proxy_store.dispatch({
                type: action_type,
                data: campaign_hover_state
            });

            this.remove();
        }
    }
}
