import { REBATES_POPUP_IFRAME_ID, REBATES_POPUP_ZINDEX }  from '../InjectionFrameIds';

import RebatesBaseController from './RebatesBaseController';

import { REBATES_CAMPAIGN_POPOUT_TIMEOUT_VALUE } from '../../../../../utilities/src/rebates_const'

export default class RebatesPopupController extends RebatesBaseController {
    constructor() {
        super(REBATES_POPUP_IFRAME_ID);
    }

    inject(proxy_store, hover_images, rebates_info, is_activated) {
        const height = is_activated ? 100 : 120;

        if (!super.inject(height, REBATES_POPUP_ZINDEX)) {
            return;
        }

        this.proxy_store = proxy_store;
        this.hover_images = hover_images;
        this.rebates_info = rebates_info;
        this.is_activated = is_activated;
    }

    static cleanup() {
        RebatesBaseController.cleanup(REBATES_POPUP_IFRAME_ID);
    }

    remove() {
        super.remove();

        this.proxy_store = null;
        this.hover_images = null;
        this.rebates_info = null;
        this.is_activated = false;
    }

    onFrameLoadedInitially() {
        this.frame.contentWindow.postMessage({
            method: "renderPopup",
            hover_images: this.hover_images,
            rebates_info: this.rebates_info,
            is_activated: this.is_activated,
        }, '*');

        if (this.is_activated) {
            this.proxy_store.dispatch({
                type: 'UPDATE_ACTIVATED_SITE',
                data: {
                    domain: this.rebates_info.domainRegex,
                    is_popup: true,
                    time: new Date().getTime(),
                }
            });
            setTimeout(() => {
                this.remove();
            }, REBATES_CAMPAIGN_POPOUT_TIMEOUT_VALUE);
        }
    }

    onWindowMessage(e) {
        const msg = e.data;
        if (!(msg && msg.method)) {
            return;
        }

        if (msg.method == 'RTBcs.RebatesManager.activatePopup') {
            const activated_sites =
                this.proxy_store.getState().data.rebates_activated_sites;
            const index_in_activated_sites =
                activated_sites.findIndex((item) => {
                    return item.domain == this.rebates_info.domainRegex;
                });
            if (index_in_activated_sites == -1) {
                this.proxy_store.dispatch({
                    type: 'ADD_ACTIVATED_SITE',
                    data: {
                        domain: this.rebates_info.domainRegex,
                        is_popup: false,
                        time: new Date().getTime(),
                    }
                });
            }

            window.location =
                this.rebates_info.link +
                "&deepLinkUrl=" + encodeURIComponent(window.location.href) +
                "&shoppingTrip=true";
        }
        else if (msg.method == 'RTBcs.RebatesManager.closePopup') {
            this.proxy_store.dispatch({
                type: 'ADD_REBATES_POPUP_CLOSE_SITE',
                data: this.rebates_domain
            });

            this.remove();
        }
    }
}
