const site_layout_specifics_table = {
  "yahooshopping" : {
    dynamic_class_to_add: ".SearchHeader__main.SearchHeader__main--isSticky",
  },
  "google" : {
    dynamic_class_to_add: ".minidiv#searchform",
  },
};

const PICKUPBAR_DOCUMENT_MARGIN_TOP_DYNAMIC_STYLE_ID = "RTB_DMTP_DynStyleBlock";

export default class DocumentMarginTopController {
  constructor(scid, transition) {
    this.margin_top = 0;
    this.transition = transition;
    this.site_layout_specifics = site_layout_specifics_table[scid];
  }

  setMarginTop(margin_top) {
    this.margin_top = margin_top;
    this.setHtmlMarginTopStyle_();

    if (this.site_layout_specifics) {
      this.fixDocumentStyle_();
    }
  }

  detach() {
    if (this.site_layout_specifics) {
      this.unfixDocumentStyle_();
    }
  }

  static cleanup() {
    const broken_dynamic_style = document.getElementById(
        PICKUPBAR_DOCUMENT_MARGIN_TOP_DYNAMIC_STYLE_ID);
    if (broken_dynamic_style) {
      document.head.removeChild(broken_dynamic_style);
    }

    const html = document.documentElement;
    html.style.transition = "";
    html.style.marginTop = "";
    html.style.position = "";
  }

  //////////////////////////////////////////////////////////////////////////////
  // Private implementation:
  //
  setHtmlMarginTopStyle_() {
    const html = document.documentElement;
    html.style.transition = "margin-top " + this.transition;
    html.style.marginTop = this.margin_top + "px";
    html.style.position = "relative";
  }

  fixDocumentStyle_() {
    this.unfixDocumentStyle_();

    if (this.site_layout_specifics) {
      this.dynamic_style = document.createElement("style");
      this.dynamic_style.setAttribute(
          'id', PICKUPBAR_DOCUMENT_MARGIN_TOP_DYNAMIC_STYLE_ID);
      this.dynamic_style.innerHTML =
          this.site_layout_specifics.dynamic_class_to_add +
          " { transition: margin-top " + this.transition + ";" +
          " margin-top: " + this.margin_top + "px !important; }";
      document.head.appendChild(this.dynamic_style);
    }
  }

  unfixDocumentStyle_() {
    if (this.dynamic_style) {
      try {
        document.head.removeChild(this.dynamic_style);
      } catch(e) {
      }
      delete this.dynamic_style;
    }
  }
}
