import RebatesCampaignController from './rebates/RebatesCampaignController';
import RebatesPopupController from './rebates/RebatesPopupController';

const FRAME_TOP_OFFSET = 5;  // `top` position of the highest frame.
const FRAME_SPACING = 5;     // space between the first and second frames.

class RebatesManagerClass {
    constructor() {
        this.popup_controller = new RebatesPopupController();
        this.popup_controller.on_visibility_changed =
            this.onFrameVisibilityChanged.bind(this);

        this.campaign_controller = new RebatesCampaignController();
        this.campaign_controller.on_visibility_changed =
            this.onFrameVisibilityChanged.bind(this);
    }

    injectPopup(proxy_store, hover_images, rebates_info, is_activated) {
        this.popup_controller.inject(proxy_store,
                                     hover_images, rebates_info, is_activated);
    }

    injectCampaign(proxy_store, toolbar_campaign) {
        this.campaign_controller.inject(proxy_store, toolbar_campaign);
    }

    cleanup() {
        this.remove();

        RebatesPopupController.cleanup();
        RebatesCampaignController.cleanup();
    }

    remove() {
        this.popup_controller.remove();
        this.campaign_controller.remove();
    }

    onFrameVisibilityChanged() {
        // One of the injected frames has become visible/invisible: update other
        // frame top position.
        const visible_frames = [];
        if (this.popup_controller.visible) {
            visible_frames.push(this.popup_controller);
        }
        if (this.campaign_controller.visible) {
            visible_frames.push(this.campaign_controller);
        }
        if (visible_frames.length > 0) {
            visible_frames[0].top = FRAME_TOP_OFFSET;
            let next_top =
                FRAME_TOP_OFFSET + visible_frames[0].height + FRAME_SPACING;
            for (let i = 1; i < visible_frames.length; ++i) {
                visible_frames[i].top = next_top;
                next_top += visible_frames[i].height + FRAME_SPACING;
            }
        }
    }
}

const RebatesManager = new RebatesManagerClass();
export default RebatesManager;
