// Sort the ids by their zIndex: a bar with higher zIndex lays over others.
// Additionally, always use `addBarNodeIntoDocument()` to inject any bars to
// gurantee that TOOLBAR_IFRAME_ID node is inserted "after" any others in the
// document.

export const TOOLBAR_IFRAME_ID = "rakutentoolbar";
export const TOOLBAR_ZINDEX = 2147483647;

export const AFFILIATEBAR_IFRAME_ID = "rakutenaffiliateitembar";
export const AFFILIATEBAR_ZINDEX = 2147483647;

export const PICKUPBAR_IFRAME_ID = "rakutenpickupbar";
export const PICKUPBAR_ZINDEX = 2147483647;

export function addBarNodeIntoDocument(bar_node) {
  const searchbar_node = document.getElementById(TOOLBAR_IFRAME_ID);
  if (searchbar_node) {
    document.body.insertBefore(bar_node, searchbar_node);
  } else {
    document.body.appendChild(bar_node);
  }
}
