import { AFFILIATEBAR_IFRAME_ID, AFFILIATEBAR_ZINDEX,
         addBarNodeIntoDocument }  from './BarIds';
import { AMAZON_ASSISTANT_DELAY, AMAZON_ASSISTANT_HEIGHT, AMAZON_ASSISTANT_IFRAME_FILTER}  from '../../../../utilities/src/common_const'
import { handleMutationCallback, handleFixedElements } from '../../../../utilities/src/common_helper'

const AFFILIATEBAR_HEIGHT = 75;

class AffiliateManagerClass {
    constructor() {
        this.frame = null;
        this.mutationObservers = [];
    }

    init(_affiliate_data) {
        this.inject_(AFFILIATEBAR_HEIGHT + "px");

        window.addEventListener('message', ((e) => {
            let msg = e.data;
            if (!(msg && msg.method)) {
                return;
            }

            if (msg.method == 'RTBcs.AffiliateManager.getData') {
                this.frame.contentWindow.postMessage({
                    method: "setData",
                    value: _affiliate_data
                }, '*');
            } else if (msg.method == 'RTBcs.AffiliateManager.overlayDivSet') {
                this.frame.style.height = "100%";
                this.frame.style.background = "rgba(0, 0, 0, 0.7)";
            } else if (msg.method == 'RTBcs.AffiliateManager.overlayDivReset') {
                this.frame.style.height = AFFILIATEBAR_HEIGHT + "px";
                this.frame.style.background = "";
            } else if (msg.method == 'RTBcs.AffiliateManager.closeToolbar') {
                this.remove();
            }
        }));

        const config = { attributes: false, childList: true, subtree: false };
        // Callback function to execute when mutations are observed
        const callback_added = (mutationsList, observer) => {
            handleMutationCallback(mutationsList, true, this.handleTopMargins);
        };
        const callback_removed = (mutationsList, observer) => {
            handleMutationCallback(mutationsList, false, this.handleTopMargins);
        };

        const targetNode = document.body;
        let observer_added = new MutationObserver(callback_added);
        observer_added.observe(targetNode, config);
        let observer_removed = new MutationObserver(callback_removed);
        observer_removed.observe(targetNode, config);

        this.mutationObservers.push(observer_added);
        this.mutationObservers.push(observer_removed);
    }

    handleTopMargins(_data) {
        let timeout = 0;
        let isAmazonAssistantVisible = false;
        if(_data && "isAmazonAssistantVisible" in _data) {
            isAmazonAssistantVisible = _data.isAmazonAssistantVisible;
            timeout = AMAZON_ASSISTANT_DELAY;
        }
        setTimeout(() => {
            //Handling of Amazon Assistant Top
            let affiliateBar = document.getElementById(AFFILIATEBAR_IFRAME_ID);
            if(affiliateBar) {
                handleFixedElements(affiliateBar, AFFILIATEBAR_HEIGHT, (isAmazonAssistantVisible?AMAZON_ASSISTANT_HEIGHT:0));
            }
        }, timeout);
    }

    setupPopupStyle(height) {
        this.frame.setAttribute('id', AFFILIATEBAR_IFRAME_ID);
        this.frame.setAttribute('scrolling', "no");
        this.frame.style.border = "none";
        this.frame.style.position = "fixed";
        this.frame.style.top = "-75px";
        this.frame.style.left = "0px";
        this.frame.style.right = "auto";
        this.frame.style.bottom = "auto";
        this.frame.style.verticalAlign = "baseline";
        this.frame.style.margin = "0";
        this.frame.style.padding = "0";
        this.frame.style.width = "100%";
        this.frame.style.height = height;
        this.frame.style.transition = "top 0.8s ease-in-out, height 0.8s ease-in-out";
        this.frame.style.zIndex = AFFILIATEBAR_ZINDEX;
    }

    inject_(height) {
        this.frame = document.createElement("iframe");
        this.setupPopupStyle(height);
        this.frame.addEventListener('load', () => {
            handleFixedElements(this.frame, AFFILIATEBAR_HEIGHT, 0);
            const amazon_iframe = document.querySelector(AMAZON_ASSISTANT_IFRAME_FILTER);
            if(amazon_iframe) {
                const node_height = amazon_iframe.offsetHeight>0?amazon_iframe.offsetHeight:parseInt(amazon_iframe.style.height, 10);
                if(node_height < 90) {
                    this.handleTopMargins({"isAmazonAssistantVisible": true});
                }
            }
        });
        this.frame.src = platform_api_root.runtime.getURL('content_affiliatebar.html');
        addBarNodeIntoDocument(this.frame);
    }

    cleanup() {
        this.frame = document.getElementById(AFFILIATEBAR_IFRAME_ID);
        this.remove();
        this.frame = null;
    }

    remove() {
        if (this.frame && this.frame.parentElement) {
            // Remove the frame after the closing animation is done.
            const frame_being_removed = this.frame;
            this.frame.addEventListener('transitionend', () => {
                document.body.removeChild(frame_being_removed);
                document.body.removeAttribute('style');
                let fixedElements = document.body.getElementsByClassName("fixed_elem_fix")
                while (fixedElements.length) {
                    fixedElements[0].classList.remove('fixed_elem_fix');
                }
            });
            this.mutationObservers.forEach(function(item, index, array) {
                item.disconnect();
            });
            this.mutationObservers = [];
            document.documentElement.style = "margin-top:0px !important;transition:margin-top 0.8s ease-in-out;";
            this.frame.style.height = 0;
            this.frame = null;
        }
    }
}

const AffiliateManager = new AffiliateManagerClass();
export default AffiliateManager;