import React, { Component } from 'react';
import PropTypes from 'prop-types'

export default class PickupBarOptions extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.cancelButton.addEventListener("click", (e) => {
            window.parent.postMessage({
                method: this.props.onCancelMsg
            }, '*');
        });
        this.okButton.addEventListener("click", (e) => {
            window.parent.postMessage({
                method: this.props.onOkMsg,
                index: this.optionsSearch.selectedIndex,
            }, '*');
        });
    }

    render() {
        const options_style = {
            "padding":"18px 17px 12px 21px",
            "fontSize": "11px",
            "fontFamily": "'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'メイリオ', Meiryo, Osaka, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif",
        }

        const drop_down_style = {
            "borderRadius": "4px",
            "boxSizing": "border-box",
            "border": "1px solid #BF0000",
            "padding": "2px",
            "margin": "0px 14px",
            "outline": "none"
        }
        const headline_style = {
            "margin": "0px",
            "padding": "0px",
            "fontSize": "13px",
            "lineHeight": "15px",
            "color": "#000"
        }
        const description_style = {
            "color": "#000",
            "fontSize": "10px",
            "lineHeight": "12px"
        }
        const button_div_style = {
            "float": "right",
            "marginTop":"7px"
        }
        const button_style = {
            "width": "100px",
            "height": "30px",
            "backgroundColor": "#303030",
            "borderRadius": "4px",
            "color": "#FFF",
            "textAlign": "center",
            "fontSize": "12px",
            "lineHeight": "14px",
            "border": "0px",
            "marginLeft": "17px"
        }

        return (
            <div id= "pickupbar_options" style = {options_style}>
                <p style={headline_style}>検索サイト上でPickup機能表示を
                <select id="options_value_search" style={drop_down_style} ref={c => this.optionsSearch = c}>
                    <option>今回だけ</option>
                    <option>24時間</option>
                    <option>30日間</option>
                </select>オフにする </p>
                <p style={description_style}>お得なクーポンやキャンペーン情報の表示がオフになりますので、ご注意ください。この設定は楽天ウェブ検索の「設定」から、いつでも変更することができます。</p>
                <div style={button_div_style}>
                    <button type='submit'  ref={c => this.cancelButton = c} style={button_style}>キャンセル</button>
                    <button type='submit'  ref={o => this.okButton = o} style={button_style}>確定する</button>
                </div>
            </div>
        );
    }
}

PickupBarOptions.propTypes = {
    onOkMsg: PropTypes.string,
    onCancelMsg: PropTypes.string,
};
