export default class RebatesBaseController {
    constructor(frame_id) {
        this.frame_id = frame_id;
        this.frame = null;
        this.visible_ = false;
    }

    set on_visibility_changed(callback) {
        this.on_visibility_changed_ = callback;
    }

    inject(height, frame_zindex) {
        if (this.frame) {
            return false;
        }

        this.frame = document.createElement("iframe");

        this.frame.setAttribute('id', this.frame_id);
        this.frame.setAttribute('scrolling', "no");
        this.frame.style.border = "none";
        this.frame.style.position = "fixed";
        this.frame.style.top = (0 - height) + "px";
        this.frame.style.right = "10px";
        this.frame.style.width = "340px";
        this.frame.style.height = height + "px";
        this.frame.style.padding = "0px";
        this.frame.style.margin = "0px";
        this.frame.style.boxShadow = "2px 0px 10px 0px #222";
        this.frame.style.borderRadius = "3px";
        this.frame.style.zIndex = frame_zindex;
        this.frame.style.display = "block";
        this.frame.style.opacity = 0;
        this.frame.style.transition = "all 0.8s ease-in-out";

        this.frame.addEventListener('load', () => {
            this.onFrameLoadedInitially();

            // Start frame showing animation.
            this.frame.style.opacity = 1;

            this.visible_ = true;
            if (this.on_visibility_changed_) {
                this.on_visibility_changed_();
            }
        });

        this.frame.src = platform_api_root.runtime.getURL('content_rebates_popup.html');
        document.body.appendChild(this.frame);

        this.addedWindowMessageHandler_ = this.onWindowMessage.bind(this);
        window.addEventListener('message', this.addedWindowMessageHandler_);
        return true;
    }

    static cleanup(frame_id) {
        const broken_frame = document.getElementById(frame_id);
        if (broken_frame) {
          document.body.removeChild(broken_frame);
        }
    }

    remove() {
        if (!this.frame) {
            return;
        }

        if (this.frame.parentElement) {
            this.frame.removeAttribute('id');
            const frame_being_removed = this.frame;
            this.frame.addEventListener('transitionend', () => {
                document.body.removeChild(frame_being_removed);
            });

            // Start frame closing animation.
            this.frame.style.opacity = 0;
        }

        if (this.addedWindowMessageHandler_) {
            window.removeEventListener('message', this.addedWindowMessageHandler_);
            delete this.addedWindowMessageHandler_;
        }

        this.frame = null;

        this.visible_ = false;
        if (this.on_visibility_changed_) {
            this.on_visibility_changed_();
        }
    }

    get visible() {
        return this.frame && this.frame.parentElement && this.visible_;
    }

    get height() {
        return (this.frame) ? parseInt(this.frame.style.height, 10) : 0;
    }

    set top(value) {
        if (this.frame) {
            this.frame.style.top = value + "px";
        }
    }

    onFrameLoadedInitially() {
    }

    onWindowMessage(e) {
    }
}
