import { TOOLBAR_IFRAME_ID, TOOLBAR_ZINDEX }  from './BarIds';

import watch from 'redux-watch';

import {KEYS} from '../../../../utilities/src/common_const';

// When a list with search box suggestions is opened, we have to increase the
// frame height so it is visible fully and user can interact with it. As soon as
// the list is closed back, we should return the frame height to normal, so user
// could interact with the underlying page normally.
const TOOLBAR_FRAME_HEIGHT_NORMAL = 36;
const TOOLBAR_FRAME_HEIGHT_EXPANDED = 560;

class ToolbarManagerClass {
    constructor() {
        this.frame = null;
        this.frame_loaded = false;
        this.bar_sticky = false;
        this.bar_enabled = false;
        this.latest_box_value = '';
    }

    init(proxyStore) {
        // Get the Search bar settings from the local storage and if "Always on"
        // is checked, do an additional check whether the "Always on" is
        // disabled for the tab.
        const searchBarSetting = proxyStore.getState().data.SearchBarSetting;
        if (searchBarSetting) {
            if (searchBarSetting.SearchBarHighlight !== undefined) {
                this.bar_enabled = searchBarSetting.SearchBarHighlight;
            }
            if (searchBarSetting.SearchBarDisplayAlways !== undefined) {
                this.bar_sticky = searchBarSetting.SearchBarDisplayAlways;
            }
        }

        if (this.bar_sticky) {
            platform_api_root.runtime.sendMessage({
                method: "getTabSearchBarStatus"
            }, (response) => {
                this.bar_sticky = response.status;
                this.showOnOffToolbar();
            });
        }
        else {
            this.showOnOffToolbar();
        }

        this._listenForDocumentEvents();
        this._listenForStoreChanges(proxyStore);
    }

    _listenForDocumentEvents() {
        // We should never attach twice to the same window/document.
        if (this._attachedToDocumentEvents) {
            return;
        }
        this._attachedToDocumentEvents = true;

        document.addEventListener("mouseup", () => {
            this.displaySelectedOnToolbar();
        });

        document.addEventListener("keyup", (event) => {
            if (event.keyCode == KEYS.KEY_ESCAPE) {
                this.offToolbar();
            }
        });

        platform_api_root.runtime.onMessage.addListener((request, sender, sendResponse) => {
            if (request.method == "displayselectedToBox") {
                if (this.readyForSelection()) {
                    this.setBoxValue(request.value);
                    this.showOnOffToolbar();
                }
            }
        });

        window.addEventListener('message', (e) => {
            var msg = e.data;
            if (!(msg && msg.method)) {
                return;
            }

            // In this handler we expect messages from the injected frame only.
            if (!this.frame) {
                return;
            }

            if (msg.method == 'RTBcs.ToolbarManager.shrink') {
                this.frame.style.height = TOOLBAR_FRAME_HEIGHT_NORMAL + "px";
            } else if (msg.method == 'RTBcs.ToolbarManager.expand') {
                this.frame.style.height = TOOLBAR_FRAME_HEIGHT_EXPANDED + "px";
            } else if (msg.method == 'RTBcs.ToolbarManager.hide') {
                this.offToolbar();
            } else if (msg.method == 'RTBcs.ToolbarManager.setBoxValue') {
                this.latest_box_value = msg.value;
            } else if (msg.method == 'RTBcs.ToolbarManager.onload') {
                this.frame_loaded = true;
                this.setBoxValue(this.latest_box_value);
                this.showOnOffToolbar();
            }
        });
    }

    _listenForStoreChanges(proxyStore) {
        // We should never attach twice to the same store object. But take into
        // account that in Edge the store object is changed when extension is
        // re-enabled.
        if (this._attachedToProxyStore === proxyStore) {
            return;
        }
        this._attachedToProxyStore = proxyStore;

        const searchBarSettingWatcher =
            watch(() => this._attachedToProxyStore.getState(),
                  'data.SearchBarSetting');
        this._attachedToProxyStore.subscribe(
            searchBarSettingWatcher((newVal, oldVal, objectPath) => {
                const need_reinit =
                    (oldVal === undefined && newVal !== undefined) ||
                    (oldVal !== undefined && newVal !== undefined &&
                        (oldVal.SearchBarHighlight != newVal.SearchBarHighlight ||
                         oldVal.SearchBarDisplayAlways != newVal.SearchBarDisplayAlways));

                if (need_reinit) {
                    this.init(this._attachedToProxyStore);
                }
            })
        );
    }

    cleanup() {
        this.frame = document.getElementById(TOOLBAR_IFRAME_ID);
        this.removeFrame_();
        this.frame = null;
        this.frame_loaded = false;
    }

    setBoxValue(text) {
        this.latest_box_value = text;
        if (this.frame && this.frame.contentWindow) {
            this.frame.contentWindow.postMessage({
                method: "setBoxValue",
                value: text
            }, '*');
        }
    }

    offToolbar() {
        this.setBoxValue('');
        this.bar_sticky = false;
        platform_api_root.runtime.sendMessage({method:"setTabSearchBarStatus", value:false});
        this.showOnOffToolbar();
    }

    showOnOffToolbar() {
        const on = this.bar_sticky || (this.latest_box_value && this.latest_box_value != '');
        if (on) {
            this.ensureFrameAdded_();
            // If loaded show it immediately. Otherwise wait until it is loaded
            // and then make it visible.
            if (this.frame_loaded) {
                this.frame.style.display = "inline";
            }
        } else {
            if (this.frame) {
                this.frame.style.display = "none";
                this.removeFrame_();
            }
        }
    }

    displaySelectedOnToolbar() {
        window.setTimeout(() => {
            if (this.readyForSelection()) {
                var selection = document.getSelection() || window.getSelection();
                if (selection) {
                    this.setBoxValue('' + selection);
                }
            }
            this.showOnOffToolbar();
        }, 0);
    }

    readyForSelection() {
        return this.bar_enabled
            && document.activeElement
            && document.activeElement.nodeName != "INPUT";
    }

    ensureFrameCreated_() {
        if (this.frame) {
            return;
        }

        this.frame = document.createElement("iframe");
        this.frame.src = platform_api_root.runtime.getURL('content_searchbar.html');

        this.frame.setAttribute('id', TOOLBAR_IFRAME_ID);
        this.frame.setAttribute('scrolling', "no");
        this.frame.style.border = "none";
        this.frame.style.position = "fixed";
        this.frame.style.top = "0px";
        this.frame.style.left = "0px";
        this.frame.style.right = "auto";
        this.frame.style.bottom = "auto";
        this.frame.style.verticalAlign = "baseline";
        this.frame.style.marginLeft = "0px";
        this.frame.style.zIndex = TOOLBAR_ZINDEX;
        this.frame.style.width = "396px";
        this.frame.style.height = TOOLBAR_FRAME_HEIGHT_NORMAL + "px";
        this.frame.style.display = "none";
    }

    ensureFrameAdded_() {
        this.ensureFrameCreated_();

        if (!this.frame.parentElement) {
            document.body.appendChild(this.frame);
        }
    }

    removeFrame_() {
        if (this.frame && this.frame.parentElement) {
            this.frame.remove();
            this.frame_loaded = false;
        }
    }
}

const ToolbarManager = new ToolbarManagerClass();
export default ToolbarManager;
